@import url(https://fonts.googleapis.com/css?family=Bebas+Neue|Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{position:fixed;width:100%;top:0;left:0;z-index:5;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}header.sticky{background:white;box-shadow:0 5px 20px rgba(0,0,0,0.04)}header .container{display:flex;justify-content:space-between;align-items:center;padding:0px 8%}button.logo{background:transparent;border:0;outline:0}nav ul{display:flex;justify-content:flex-end;align-items:center;padding:0;margin:0}nav li{list-style:none;padding:0;margin:0}nav button{position:relative;background:transparent;outline:0;border:0;font-family:'Bebas Neue', cursive;font-size:24px;padding:10px 0;margin:20px}nav button:after{position:absolute;content:"";width:0;height:3px;background:#1F1F1F;bottom:0;left:0;right:0;margin:auto;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}nav button:hover:after,nav button:focus:after{width:100%}button.mobile-trigger{display:none;position:relative;background:transparent;width:40px;height:25px;border:0;outline:0;-webkit-transform:translateY(-5px);transform:translateY(-5px)}button.mobile-trigger span{position:absolute;width:100%;height:4px;background:#1F1F1F;left:0;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}button.mobile-trigger span:nth-child(1){top:0}button.mobile-trigger span:nth-child(2){top:calc(50% - 1.5px);right:0;margin:auto}button.mobile-trigger span:nth-child(3){bottom:0}button.mobile-trigger.open span:nth-child(1){-webkit-transform:rotate(45deg);transform:rotate(45deg);top:calc(50% - 1.5px)}button.mobile-trigger.open span:nth-child(2){width:0}button.mobile-trigger.open span:nth-child(3){-webkit-transform:rotate(-45deg);transform:rotate(-45deg);top:calc(50% - 1.5px);bottom:auto}@media screen and (max-width: 767px){header .container{padding-top:20px;padding-bottom:20px}header.open{background:white}header.open nav{opacity:1;visibility:visible;left:0}button.mobile-trigger{display:block}nav{position:fixed;width:100%;height:calc(100vh - 75px);top:75px;left:100%;background:white;visibility:hidden;opacity:0;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}nav ul{flex-direction:column;justify-content:center;padding-top:15vh}nav button{font-size:50px;margin:2vh 0}}

.social-group{margin:20px -8px}.social-group a{display:inline-block;margin:8px;color:inherit;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.social-group a:hover,.social-group a:focus{-webkit-transform:translateY(-3px);transform:translateY(-3px)}.social-group a.icon-instagram:hover svg path,.social-group a.icon-instagram:focus svg path{fill:#962fbf}.social-group a.icon-linkedin:hover svg path,.social-group a.icon-linkedin:focus svg path{fill:#0072b1}.social-group a.icon-twitter:hover svg path,.social-group a.icon-twitter:focus svg path{fill:#00aced}.social-group a.icon-youtube:hover svg path,.social-group a.icon-youtube:focus svg path{fill:#ff0000}.social-group svg{height:25px}.social-group svg path{-webkit-transition:all 0.05s ease;transition:all 0.05s ease}@media screen and (max-width: 767px){.social-group{margin:15px -8px}.social-group svg{height:20px}}

.banner{position:relative;background:linear-gradient(45deg, #DDEDFB, #D9E7F3)}.banner:before{position:absolute;content:"";width:100%;height:20vw;background:white;left:0;bottom:-15vw;-webkit-transform:skewY(5deg);transform:skewY(5deg);z-index:1}.banner .container{position:relative;display:flex;justify-content:space-around;align-items:center;padding-top:275px;padding-bottom:275px}.banner .content-wrapper{position:relative;width:35%;text-align:right;margin-left:0;margin-right:auto;z-index:1}.banner .img-wrapper{position:absolute;bottom:0;right:10%;max-width:650px}.banner h1{font-size:70px;line-height:1}.banner h2{font-size:36px}@media screen and (max-width: 1100px){.banner .content-wrapper{width:40%}.banner .img-wrapper{right:0}}@media screen and (max-width: 950px){.banner .content-wrapper{text-align:left;width:100%}.banner .img-wrapper{width:70%}}@media screen and (max-width: 767px){.banner .container{overflow-x:hidden;padding-top:150px;padding-bottom:150px}.banner h1{font-size:50px}.banner h2{font-size:26px}.banner .img-wrapper{right:-10%}}

.about{position:relative;z-index:1}.about .container{display:flex;justify-content:space-between;align-items:flex-start;padding-top:150px}.about .content-wrapper{width:50%}.about .img-wrapper{width:40%}@media screen and (max-width: 950px){.about .container{flex-direction:column;padding-top:100px}.about .content-wrapper{width:100%}.about .img-wrapper{margin:0 auto 50px;max-width:500px;width:80%}}

@-webkit-keyframes modal-video{from{opacity:0}to{opacity:1}}@keyframes modal-video{from{opacity:0}to{opacity:1}}@-webkit-keyframes modal-video-inner{from{-webkit-transform:translate(0, 100px);transform:translate(0, 100px)}to{-webkit-transform:translate(0, 0);transform:translate(0, 0)}}@keyframes modal-video-inner{from{-webkit-transform:translate(0, 100px);transform:translate(0, 100px)}to{-webkit-transform:translate(0, 0);transform:translate(0, 0)}}.modal-video{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.5);z-index:1000000;cursor:pointer;opacity:1;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-duration:.3s;animation-duration:.3s;-webkit-animation-name:modal-video;animation-name:modal-video;-webkit-transition:opacity .3s ease-out;transition:opacity .3s ease-out}.modal-video-effect-exit{opacity:0}.modal-video-effect-exit .modal-video-movie-wrap{-webkit-transform:translate(0, 100px);transform:translate(0, 100px)}.modal-video-body{max-width:940px;width:100%;height:100%;margin:0 auto;display:table}.modal-video-inner{display:table-cell;vertical-align:middle;width:100%;height:100%}.modal-video-movie-wrap{width:100%;height:0;position:relative;padding-bottom:56.25%;background-color:#333;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-duration:.3s;animation-duration:.3s;-webkit-animation-name:modal-video-inner;animation-name:modal-video-inner;-webkit-transform:translate(0, 0);transform:translate(0, 0);-webkit-transition:-webkit-transform .3s ease-out;transition:-webkit-transform .3s ease-out;transition:transform .3s ease-out;transition:transform .3s ease-out, -webkit-transform .3s ease-out}.modal-video-movie-wrap iframe{position:absolute;top:0;left:0;width:100%;height:100%}.modal-video-close-btn{position:absolute;z-index:2;top:-35px;right:-35px;display:inline-block;width:35px;height:35px;overflow:hidden;border:none;background:transparent}.modal-video-close-btn:before{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.modal-video-close-btn:after{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.modal-video-close-btn:before,.modal-video-close-btn:after{content:'';position:absolute;height:2px;width:100%;top:50%;left:0;margin-top:-1px;background:#fff;border-radius:5px;margin-top:-6px}.video-wrapper{position:relative;width:calc(100% / 3 - 20px);height:15vw;max-height:250px;margin:10px;background-size:cover;background-position:center}.video-wrapper button.open-video{display:flex;justify-content:center;align-items:center;position:absolute;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.2);border:0;outline:0;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.video-wrapper button.open-video:hover,.video-wrapper button.open-video:focus{background:transparent}.video-wrapper button.open-video:hover .play,.video-wrapper button.open-video:focus .play{opacity:1}.video-wrapper .play{width:50px;background:white;border-radius:50%;padding:18px 6px 18px 12px;opacity:0.7;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.video-wrapper .play svg{width:25px}@media screen and (max-width: 767px){.video-wrapper{width:calc(50% - 20px);height:23vw}}@media screen and (max-width: 500px){.video-wrapper{width:calc(100% - 20px);height:47vw}}

.videos-group{display:flex;justify-content:flex-start;align-items:flex-start;flex-wrap:wrap}


.articles-group{display:flex;justify-content:flex-start;align-items:stretch;flex-wrap:wrap}

.article{display:flex;justify-content:space-between;align-items:flex-start;flex-direction:column;width:calc(100% / 3 - 80px);min-width:250px;max-width:calc(100% - 80px);margin:10px;padding:30px;background:#F8FAFD;color:inherit;text-decoration:none;flex-grow:1}.article img{max-height:75px;width:auto;margin-bottom:30px}.article h3{font-size:22px;line-height:1.2;font-family:'Lato', sans-serif}.article p{font-size:16px}

.media h2{text-align:center}.media-tabs-group{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;padding:0;margin:40px 0}.media-tabs-group li{list-style:none;padding:0}.media-tab{background:transparent;border:1px solid #3A3A3A;font-size:20px;padding:10px 30px 8px;border-radius:2px;font-family:'Bebas Neue', cursive;margin:8px;outline:0;opacity:0.5;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.media-tab:hover:not(.active),.media-tab:focus:not(.active){opacity:1;border:2px solid #3A3A3A;margin:7px}.media-tab.active{background:#3A3A3A;border:2px solid #3A3A3A;color:white;opacity:1}.media-content:not(.active){display:none}@media screen and (max-width: 767px){.media-tabs-group{margin:20px 0 40px}.media-tabs-group li{flex-grow:1}.media-tab{width:calc(100% - 16px)}}

.instagram-wrapper{display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap;width:50%}.instagram-wrapper span{width:calc(100% / 3 - 20px);max-height:175px;height:12vw;margin:10px;background:lightgrey}@media screen and (max-width: 767px){.instagram-wrapper{width:calc(100% + 20px);margin:50px -10px 0}.instagram-wrapper span{height:20vh;max-height:200px}}@media screen and (max-width: 500px){.instagram-wrapper span{width:calc(50% - 20px);height:40vw}.instagram-wrapper span:nth-child(n+5){display:none}}

.MuiButton-containedPrimary.custom-submit{border-radius:3px;background:#3A3A3A;margin-top:10px;padding:10px 35px 8px;font-size:20px;text-align:center;outline:0;border-radius:2px;font-family:'Bebas Neue', cursive;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.MuiButton-containedPrimary.custom-submit:hover,.MuiButton-containedPrimary.custom-submit:focus{background:#515151;-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2)}.contact form{margin-top:40px}.contact form h3{font-size:20px;font-family:'Lato', sans-serif;margin-bottom:20px}.contact .form-content>div{width:100%;margin-bottom:20px}.contact .form-content .MuiInputBase-inputMultiline{min-height:80px}.contact .form-content p.success-message{font-size:16px;font-style:italic}

.contact-wrapper{display:flex;justify-content:space-between;align-items:flex-start;margin-top:40px}.contact-wrapper .content-wrapper{width:40%}.email-wrapper{display:flex;justify-content:flex-start;align-items:center}.email-wrapper svg{height:20px}.email-wrapper a{margin-left:10px;color:inherit;text-decoration:none}.email-wrapper a:hover,.email-wrapper a:focus{text-decoration:underline}@media screen and (max-width: 767px){.contact-wrapper{flex-direction:column}.contact-wrapper .content-wrapper{width:100%}.contact form h3{font-size:18px}}

footer{background:#DEE8F1;text-align:center}footer .container{padding:15px 8%}footer p{font-size:12px;margin:0}footer img{height:30px;vertical-align:middle}

.center-btn{text-align:center}a.btn{color:inherit}button:hover{cursor:pointer}.btn{display:inline-block;text-decoration:none;padding:15px 35px 13px;margin-top:20px;font-size:22px;text-align:center;outline:0;border-radius:2px;font-family:'Bebas Neue', cursive;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.btn.btn-cta{background:#3A3A3A;color:white;border:0}.btn.btn-cta:hover,.btn.btn-cta:focus{background:#515151;-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2)}.btn.btn-hollow{border:1px solid;background:transparent}.btn.btn-hollow:hover,.btn.btn-hollow:focus{-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2)}

body{font-family:"Lato",sans-serif;color:#1F1F1F}h1,h2,h3,h4,h5{font-family:"Bebas Neue",cursive;margin:0;line-height:1.4}h2{font-size:70px}p,li{font-size:20px;line-height:1.6}img{max-width:100%}.container{padding:80px 8%;max-width:1400px;margin:auto}#about,#media,#contact{outline:0}@media screen and (max-width: 767px){.container{padding:50px 6%}h1,h2{letter-spacing:-1px}h2{font-size:50px}}

